import { Box } from "@mui/material";
import { TypographyComponent } from "../../atoms/typography";
import { Song } from "../../molecules/song";

interface BucketProps {
  bucket:any;
}
export const Bucket = (props:BucketProps) =>{
   return (
    <>
    <Box display={'grid'} gridTemplateColumns={'200px 600px'} alignItems={'bottom'}  justifyContent={'center'} bgcolor={'black'} height={'100px'} width={'100px'}>
    <TypographyComponent sx={{'display':'inline-block'}} variant="subtitle1" children={props.bucket.id} />
    {props.bucket.songs.map((item:any) => {<>
            <Song Name={item.name}  Link={item.link}/>
    </>
               
            })}

            
            
    </Box>
    </>
   )
}
